import React, { Component } from 'react';
import { Container, Row, Col,} from 'react-bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faThumbsUp } from '@fortawesome/free-solid-svg-icons'


class Thanks extends React.Component {

    render() {

        let number = "";

        if (typeof window !== 'undefined') {
        number = localStorage.getItem('number');
        }

        return (
            <>

                <Container fluid className="bg-white" id="thanks">
                    <Row className="mx-md-5 mx-4 text-center">
                        <Col>
                            <FontAwesomeIcon icon={faThumbsUp} />
                            <h1>Thank You</h1>
                            <p>Thank You for your interest in the project. Our sales executive will get in touch with you shortly.</p>
                            <p>To Get Instant Information Please Call</p>
                            <a href={`tel:${number}`}>{number}</a>
                        </Col>
                    </Row>
                </Container>

            </>
        )
    }
}
export default Thanks;