import * as React from "react";
import { Helmet } from "react-helmet";
import Navbar from '../components/navbar';
import Thanks from '../components/thanks';
import Footer from '../components/footer';
import "/src/css/custom.css";
import { useCookies } from 'react-cookie';

// styles
const pageStyles = {
    color: "#232129",
    fontFamily: "-apple-system, Roboto, sans-serif, serif",
}

// markup
const ThanksPage = () => {

    const [cookies, setCookie] = useCookies(['logo']);

    // ----- Logo -----//
    
    let project = "";
    let logo = cookies.logo;
    let rerano = "";
    let disclaimer = "";
    let copyright = "";

    if (typeof window !== 'undefined') {
        project = localStorage.getItem('project');
        //logo = localStorage.getItem('logo');
        rerano = localStorage.getItem('rerano');
        disclaimer = localStorage.getItem('disclaimer');
        copyright = localStorage.getItem('copyright');
    }
    

    return (
        <main style={pageStyles}>
            <Helmet>
                <title>{project}</title>
            </Helmet>

            <Navbar logo={logo} />
            <Thanks />
            <Footer rerano={rerano} disclaimer={disclaimer} copyright={copyright} />

        </main>

    )

}

export default ThanksPage
